<template>
  <div>
    <Head title="商品搜索" :show="true"></Head>
    <van-search
      v-model="keyword"
      placeholder="请输入搜索关键词"
      show-action
      @search="onSearch"
      @cancel="onCancel"
    />
     <list :goods="seachGoods" @changeallgoods="getNewSearchGoods" v-if="seachGoods.length>0"></list>
  </div>
</template>
<script>
import {mapState,mapActions} from 'vuex'
export default {
  computed:{
    ...mapState({
      seachGoods:'searchList'
    }),
    //vuex + v-model的数据修改
    keyword:{
      get(){
        return this.$store.state.keyword;
      },
      set(newVal){
        this.$store.commit('getKeyword',newVal);
      }
    }
  },
  data() {
    return {
      // keyword: "",
      // seachGoods:[]
    };
  },
  mounted(){
  },
  methods:{
    ...mapActions(['getSearchList']),
    onSearch(){
      //enter键盘回车事件
      this.getSearchList({
        url:"vue/getGoods",
        params:{
          keyword: this.keyword
        }
      })
      // this.$axios.get("vue/getGoods",{
      //   params:{
      //     keyword: this.keyword
      //   }
      // }).then(res=>{
      //   this.seachGoods = res.data.result;
      // })
     
    },
    onCancel(){
      console.log("用户点击搜索框右侧取消按钮时触发");
    },
    getNewSearchGoods(){
      this.seachGoods.reverse();
    }
  }
};
</script>